.form {
  padding: 102px 0 166px 0;

  &__container {
    display: flex;
  }

  &__content {
    margin-right: 122px;
    min-width: 539px;
    max-height: 727px;
    padding: 52px 48px 55px 48px;
    background: var(--light-color);
    box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.05);
    border-radius: 8px;
  }

  &__item {
    margin-bottom: 30px;
    display: flex;
    flex-direction: column;
  }

  &__label {
    margin: 0;
    margin-bottom: 6px;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0.005em;
    color: #8F90A6;
  }

  &__input {
    padding: 0 10px;
    height: 60px;
    background: var(--light-color);
    border: 1px solid #E3E7F6;
    border-radius: 6px;
  }

  &__textarea {
    max-height: 148px;
    padding: 15px;
    background: var(--light-color);
    border: 1px solid #E3E7F6;
    border-radius: 6px;
    resize: none;
  }

  &__descr {
    display: flex;
    flex-direction: column;
    margin-top: 144px;
  }

  &__article {
    display: flex;
    flex-direction: column;
    max-width: 366px;
    margin-bottom: 72px;
  }

  &__title {
    margin: 0;
    margin-bottom: 25px;
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.005em;
    color: #000000;

    & img {
      margin-right: 20px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0.005em;
    color: #000000;
  }
}
